import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useNavigate, Link } from 'react-router-dom';

const Profile = () => {
  const [userData, setUserData] = useState({
    username: '',
    email: '',
    age: '',
    bio: '',
    avatar: ''
  });
  const [error, setError] = useState('');
  const navigate = useNavigate();
  const [users, setUsers] = useState([]);
  const [friends, setFriends] = useState([]);
  const [blacklist, setBlacklist] = useState([]);
const user = localStorage.getItem('user');
const parsedUser = JSON.parse(user); //    
  useEffect(() => {
    //     
    const fetchUsers = async () => {
      try {
        const response = await axios.get('http://localhost:5000/users');
        setUsers(response.data);
      } catch (err) {
        setError('Error fetching users');
        console.error(err);
      }
    };

    //   
    const fetchFriends = async () => {
      try {
        const response = await axios.get(`http://localhost:5000/users/${parsedUser.id}/friends`);

        setFriends(response.data);
      } catch (err) {
        setError('Error fetching friends');
        console.error(err);
      }
    };

    //   
    const fetchBlacklist = async () => {
      try {
        const response = await axios.get(`http://localhost:5000/users/${parsedUser.id}/blacklist`);
        setBlacklist(response.data);
      } catch (err) {
        setError('Error fetching blacklist');
        console.error(err);
      }
    };

    fetchUsers();
    fetchFriends();
    fetchBlacklist();
  }, []);
  useEffect(() => {
    //     localStorage
    const user = localStorage.getItem('user');
    if (user) {
      const parsedUser = JSON.parse(user);
      setUserData({
        username: parsedUser.username || '',
        email: parsedUser.email || '',
        age: parsedUser.age || '',
        bio: parsedUser.bio || '',
        avatar: parsedUser.avatar || ''
      });
    }
  }, []);

  const handleChange = (e) => {
    setUserData({ ...userData, [e.target.name]: e.target.value });
  };

  const handleSave = async () => {
    const user = localStorage.getItem('user');
    if (!user) {
      setError('    localStorage');
      return;
    }

    const parsedUser = JSON.parse(user);

    try {
      //    
      const response = await axios.put(`http://localhost:5000/users/${parsedUser.id}`, {
        ...userData,
        role: parsedUser.role //   
      });

      //    localStorage
      localStorage.setItem('user', JSON.stringify(response.data));

      alert('   ');
    } catch (err) {
      setError('    ');
      console.error(err);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    navigate('/home');
  };

  const handleAddFriend = async (userId) => {
    try {
      await axios.post(`http://localhost:5000/users/${parsedUser.id}/friends`, { friendId: userId });
      setFriends((prevFriends) => [...prevFriends, users.find(user => user.id === userId)]);
    } catch (err) {
      setError('Error adding friend');
      console.error(err);
    }
  };

  const handleRemoveFriend = async (userId) => {
    try {
      await axios.delete(`http://localhost:5000/users/${parsedUser.id}/friends/${userId}`);
      setFriends(friends.filter(friend => friend.id !== userId));
    } catch (err) {
      setError('Error removing friend');
      console.error(err);
    }
  };

  const handleAddToBlacklist = async (userId) => {
    try {
      await axios.post(`http://localhost:5000/users/${parsedUser.id}/blacklist`, { blockedId: userId });
      setBlacklist((prevBlacklist) => [...prevBlacklist, users.find(user => user.id === userId)]);
    } catch (err) {
      setError('Error adding to blacklist');
      console.error(err);
    }
  };

  const handleRemoveFromBlacklist = async (userId) => {
    try {
      await axios.delete(`http://localhost:5000/users/${parsedUser.id}/blacklist/${userId}`);
      setBlacklist(blacklist.filter(blocked => blocked.id !== userId));
    } catch (err) {
      setError('Error removing from blacklist');
      console.error(err);
    }
  };
  return (
    <div>
      <div className="profile-page-header">
        <Link to="/home" className="logo">
          <h1>VISIOTOK</h1>
        </Link>
      </div>
      <div className="profile-page-body">
        <h1>Profile</h1>
        <form className="profile-form">
          <input
            className="profile-form-input"
            type="text"
            name="username"
            placeholder=""
            value={userData.username}
            onChange={handleChange}
          />
          <input
            className="profile-form-input"
            type="email"
            name="email"
            placeholder="Email"
            value={userData.email}
            onChange={handleChange}
          />
          <input
            className="profile-form-input"
            type="number"
            name="age"
            placeholder=""
            value={userData.age}
            onChange={handleChange}
          />
          <input
            className="profile-form-input"
            type="text"
            name="bio"
            placeholder=""
            value={userData.bio}
            onChange={handleChange}
          />
          <input
            className="profile-form-input"
            type="text"
            name="avatar"
            placeholder=""
            value={userData.avatar}
            onChange={handleChange}
          />
          <button type="button" className="save-profile-button" onClick={handleSave}>
            
          </button>
          <button type="button" className="logout-profile-button" onClick={handleLogout}>
            
          </button>
        </form>
        {error && <p style={{ color: 'red' }}>{error}</p>}
        <div className='profile-page-body'>
        <h1></h1>
        <div className='firends-list'>
          {error && <p style={{ color: 'red' }}>{error}</p>}
          <ul>
            {users.map(user => (
              <li className='li-user' key={user.id}>
                {user.username}
                {!friends.find(friend => friend.id === user.id) && (
                  <button className='friend-button' onClick={() => handleAddFriend(user.id)}>  </button>
                )}
                {friends.find(friend => friend.id === user.id) && (
                  <button className='friend-button' onClick={() => handleRemoveFriend(user.id)}>  </button>
                )}
                {!blacklist.find(blocked => blocked.id === user.id) && (
                  <button className='friend-button' onClick={() => handleAddToBlacklist(user.id)}>   </button>
                )}
                {blacklist.find(blocked => blocked.id === user.id) && (
                  <button className='friend-button' onClick={() => handleRemoveFromBlacklist(user.id)}>   </button>
                )}
              </li>
            ))}
          </ul>
        </div>
        <div className='firends-list'>
        <h2 style={{marginBottom:'10px'}}></h2>
        <ul>
          {friends.map(friend => (
            <li className='li-user' key={friend.id}>{friend.username} ({friend.email})</li>
          ))}
        </ul>
        </div>
        <div className='firends-list'>

        <h2 style={{marginBottom:'10px'}}> </h2>
        <ul>
          {blacklist.map(blocked => (
            <li className='li-user' key={blocked.id}>{blocked.username} ({blocked.email})</li>
          ))}
        </ul>
        </div>
      </div>
      </div>
      
    </div>
  );
};

export default Profile;
