import { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { v4 } from 'uuid';
import socket from '../../socket';
import ACTIONS from '../../socket/actions';
export default function MainPage() {
  const navigate = useNavigate();
  const [rooms, updateRooms] = useState([]);
  const [roomIdInput, setRoomIdInput] = useState('');
  const [error, setError] = useState('');
  const rootNode = useRef();
  
  //    localStorage
  const isAuthenticated = localStorage.getItem('token');
  const user = JSON.parse(localStorage.getItem('user'));  
  
  //        
  const userRole = user ? user.role : null;
  const username = user ? user.username : null;

  useEffect(() => {
    socket.on(ACTIONS.SHARE_ROOMS, ({ rooms = [] }) => {
      if (rootNode.current) {
        updateRooms(rooms);
      }
    });
    console.log(user);
  }, []);
  
  useEffect(() => {
    const handleRoomEnded = ({ message }) => {
      console.log('ROOM_ENDED event received:', message);
      alert(message); //  
      navigate('/'); //    
    };
    
    socket.on(ACTIONS.ROOM_ENDED, handleRoomEnded);
    
    return () => {
      socket.off(ACTIONS.ROOM_ENDED, handleRoomEnded);
    };
  }, [navigate]);

  const handleInputJoinRoom = () => {
    navigate(`/room/${roomIdInput}`);
  };
  console.log(user);
  
  return (
    <div className='main-page' ref={rootNode}>
      <div className='main-page-header'>
      <Link to="/home" className='logo' >
        <h1>VISIOTOK</h1>
      </Link>
     
      {isAuthenticated ? (
          <>
            <span className='welcome-span'
           
              onClick={() => navigate('/profile')}
            >
               {username}
            </span>
            {userRole === 'ADMIN' && (
              <div>
              <button className='users-admin-button'
                style={{ marginLeft: '10px' }}
                onClick={() => navigate('/users')}
              >
                
              </button>
              <button  className='statistic-admin-button'
                  style={{ marginLeft: '10px' }}
                  onClick={() => navigate('/stats')}
                >
                  
                </button>
              </div>
            ) }
          </>
        ) : (
          <button className='login-button' onClick={() => navigate('/login')}></button>
        )}
      </div>
      <div className='main-page-body'>
      <div className='room-manage-div'>
      <div className="join-room-button-input">
        <input 
          type="text" 
          placeholder=' ID '
          className="join-room-input"  
          value={roomIdInput}
          onChange={(e) => setRoomIdInput(e.target.value)}
        />
        <button type="button"    className="join-room-button"  onClick={handleInputJoinRoom}>  </button>
        {error && <p style={{ color: 'red' }}>{error}</p>}
      </div> 
      <button className="join-room-button" onClick={() => {
        navigate(`/room/${v4()}`);
      }}>  </button>
</div>
{
userRole === 'ADMIN' && (
  <div>
    <h3 style={{marginBottom:'20px'}}> </h3>
            <ul className='ul-rooms'>
              
        {rooms.map(roomID => (

          <li key={roomID}>
            {roomID}
            <button style={{marginLeft:'10px'}} className='join-room-button' onClick={() => {
              navigate(`/room/${roomID}`);
              console.log(`Entering room : ${roomID}`);
            }}>JOIN ROOM</button>
            { userRole === 'ADMIN' ? 
              <button className='join-room-button' onClick={() => {
                console.log(`Ending room with ID: ${roomID}`);
                socket.emit(ACTIONS.END_ROOM, { roomID });
              }}>END</button> : <div></div> 
            }
          </li>
        ))}
      </ul>
      </div>
)
}

</div>
    </div>
  );
}
